import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import map from 'lodash/map';
import uniq from 'lodash/uniq';

import BigCalendar from 'react-big-calendar';
import moment from 'moment';
import 'react-big-calendar/lib/css/react-big-calendar.css';

import colors from 'open-color/open-color.json';

import Box from './components/Box';

BigCalendar.momentLocalizer(moment);

const availableColors = [
  'red',
  'violet',
  'blue',
  'green',
  'yellow',
];

class Calender extends PureComponent {
  state = {
    projectList: uniq(map(this.props.events, '')),
    personList: uniq(map(this.props.events, '')),
  }

  projects = {
    byName: {},
    count: 0
  }

  persons = {
    byName: {},
    count: 0
  }

  parseColor = (type, name) => {
    const items = this[type];
    let color = items.byName[name];
    if (color) return color; // 

    const colorKey = availableColors[items.count % availableColors.length]; // 
    items.count += 1; // 
    color = colors[colorKey][5]; // opencolor
    items.byName[name] = color; // 
    return color;
  }

  parseProjectColor = (projectName) => this.parseColor('projects', projectName)

  handleProjectChange = (evt) => {
    this.setState({ selectedProject: evt.target.value })
  }

  handlePersonChange = (evt) => {
    this.setState({ selectedPerson: evt.target.value })
  }

  projectFilter = (event) => {
    const { selectedProject } = this.state;
    if (!selectedProject || selectedProject === 'none') return true;
    return event[''] === selectedProject;
  }

  personFilter = (event) => {
    const { selectedPerson } = this.state;
    if (!selectedPerson || selectedPerson === 'none') return true;
    return event[''] === selectedPerson;
  }

  render() {
    const { events } = this.props;
    const {
      projectList,
      personList,
    } = this.state;
    return (
      <div>
        <Box pb="2em">
          <select onChange={this.handleProjectChange}>
            <option value="none">----</option>
            {projectList.map((project) => (
              <option key={project}>{project}</option>
            ))}
          </select>
          <select onChange={this.handlePersonChange}>
            <option value="none">----</option>
            {personList.map((person) => (
              <option key={person}>{person}</option>
            ))}
          </select>
        </Box>
        <Box pt="66%" position="relative">
          <Box position="absolute" top="0" right="0" bottom="0" left="0">
            <BigCalendar
              events={events.filter(this.projectFilter).filter(this.personFilter)}
              defaultDate={new Date()}
              titleAccessor={(event) => `${event['']} | ${event['']} by ${event['']}`}
              startAccessor=""
              endAccessor=""
              eventPropGetter={(event) => ({
                style: {
                  backgroundColor: this.parseProjectColor(event['']),
                  opacity: event[''] ? 0.2 : 1,
                }
              })}
            />
          </Box>
        </Box>
      </div>
    );
  }
}

Calender.propTypes = {
  events: PropTypes.array,
};

export default Calender;



